.TodoSearch {
  background: transparent;
  border-radius: 20px;
  border: 2px solid #202329; 
  margin: 0 24px;
  height: 32px;
  width: calc(100% - 62px);
  font-size: 20px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F; 
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  outline: transparent;
}

.TodoSearch--loading{
  opacity: 25%;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 100;
  font-size: medium;
}

.TodoSearch:focus {
  outline-color: #2a87a1;
}

